@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0%;
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: normal;
  background-image: url(/static/media/background.9110056b.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.main_container-header {
  color: white;
  width: 526px;
  margin: auto;
  margin-top: 125px;
  display: flex;
  flex-direction: row;
}

.main_container-header .left_maincont-header {
  width: 405px;
  padding-left: 4px;
  padding-right: 5px;
}

.main_container-header .left_maincont-header h3 {
  font-style: normal;
  font-weight: 400;
  size: 24px;
  margin: 0%;
}

.main_container-header .left_maincont-header h1 {
  margin: 0%;
  size: 48px;
  font-weight: 400;
}

.main_container-header .left_maincont-header .preview {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  border-right: 3px solid #FFFFFF;
}

.main_container-header .left_maincont-header .preview .state {
  display: block;
  width: 50px;
  height: 50px;
}

.main_container-header .left_maincont-header .preview .state:hover {
  cursor: pointer;
}

.main_container-header .left_maincont-header .preview h3 {
  padding-bottom: 10px;
  padding-left: 0px;
}

.main_container-header .left_maincont-header .preview #reactplayer {
  height: 100px;
  width: 100px;
}

.main_container-header .Right_maincont-header img {
  height: 121px;
  width: 121px;
  padding-top: 5px;
}

.links {
  width: 506px;
  margin: auto;
  margin-top: 46px;
  background: #FFFFFF;
  box-shadow: 7px 9px 10px 2px rgba(0, 0, 0, 0.37);
  border-radius: 5px;
  padding: 20px;
  text-align: justify;
}

.links .button {
  border-bottom: 1px solid #b0b0b073;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0px 10px 0px;
}

.links .button img {
  padding-bottom: 5px;
}

.links .button a {
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  display: inline-block;
  padding: 14px;
  padding-left: 30px;
  padding-right: 30px;
  border: 1px solid black;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
  color: black;
  transition-property: all;
  transition-duration: 0.3s;
}

.links .button a:hover {
  background-color: black;
  color: white;
}

.links #itunes {
  padding-top: 15px;
  padding-bottom: 15px;
}
